import riot from 'riot'
import './parts/header.tag'

import root from './root'
riot.tag2('app-root', '<parts-header></parts-header> <textarea ref="textarea" rows="2" onchange="{onEdit}" oninput="{onEdit}">{inputValue}</textarea> <span class="result">{result}</span>', 'app-root >textarea,[data-is="app-root"] >textarea{width:100%} app-root >.result,[data-is="app-root"] >.result{white-space:pre-line}', '', function(opts) {
    root(this)
});
